/* text */

.first-point-login-modal{
    font-family: "Jost";
    font-size: 30px;
    color: #555;
    letter-spacing: 1px;
}

.second-point-login-modal{
    display: flex;
    flex-direction: row; 
    justify-content: center;
    font-family: "Jost";
    font-weight: 500;
    font-size: 14px;
}

.first-text-login-modal{
    position: relative;
    top: 6px;
}

/* fun. */

.second-text-login-modal{
    margin-left: 4px;
    color: lightseagreen;
    font-weight: 400;
    font-size: 23px;
    font-family: times new roman;
    letter-spacing: 1px;
}

/* inputs and labels */

.body-container-login-modal{
    display: flex;
    flex-direction: column;
    line-height: 3.5;
    text-align: left;
}

.MuiFilledInput-root{
    border-radius: 0px;
}

.MuiFormControl-root.MuiTextField-root.input-login-modal {
    width: 95%; 
}

/* position and font of button */

.text-button-login-modal{
    float: left;
    position: relative;
    top: 10px;
    left: 10px;
    font-family: "Jost";
    font-size:12px;
    color: #2F2D4A;
}

/* footer */

.footer-flex-login-modal{
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-around;
    width: 100%;
    position: relative;
    top: 25px;
}

.line-login-modal{
    display: block;
    height: 1px;
    border-top: 1px solid rgb(17, 13, 13);
    border-right-width: 0;
    border-bottom-width: 0;
    border-left-width: 0;
    margin: 1em 0;
    padding: 0;
    width: 100%;
    position: inherit;
    right: 0;
}
