/* logo */

.logostyle {
	width: 80px;
	height: 80px;
	margin-left: 20px;
	margin-top: 2.5px;
}

/* parent element - This control the box on the back -
 all the other elements derived from this */

.nav {
	display: flex;
	align-items: center;
	height: 50px;
	background-color: #2e856e;
	font-family: "Times New Roman";
	font-weight: 500;
	font-size: 18px;
	grid-gap: 15px;
	gap: 15px;
	margin-bottom: 25px; /* moves the elements below the nav bar */
}

/* child element - This controls the smaller box 
from the parent meaning the links */

.nav-links {
	display: flex;
	justify-content: space-around;
	width: 50%;
}

/* remove bullets */

.nav-links li {
	list-style: none;
}

/* each link style */

.nav-links a,
#logout {
	color: white;
	text-decoration: none;
	letter-spacing: 1px;
}

/* hovering the mouse in the element */

.nav-links a:hover,
#logout:hover {
	cursor: pointer;
	color: burlywood;
}

/* menu style and not displaying */

.menu-open {
	display: none;
}

.menu-open {
	color: white;
	transform: scale(1.6);
}

/* small screen resizing */

@media screen and (max-width: 800px) {
	body {
		overflow-x: hidden;
	}

	/* small nav goes inside the page */

	.nav-links {
		background-color: #2e856e;
		position: absolute;
		text-align: center;

		/* padding-left: 10px; */
		
		right: 0px;
		min-height: 92vh;
		top: 50px;
		margin-left: 20px;
		display: flex;
		flex-direction: column;
		width: 35%;
		transform: translateX(100%);
		transition: transform 0.2s ease-in;
	}

	.nav-links li {
		opacity: 0;
	}

	.menu-open {
		cursor: pointer;
		position: relative;
		top: 2px;
		display: block;
		margin: 75%;
	}
}

/* small nav is outside the page */

.nav-active {
	transform: translateX(0%);
}

/* links fading in animation */

@keyframes navLinksFade {
	from {
		opacity: 0;
		transform: translateX(48px);
	}
	to {
		opacity: 1;
		transform: translateX(0px);
	}
}
