.chat-window {
  padding: 20px 20px 10px 20px;
  width: calc(100vw - 500px);
  max-width: 1000px;
  border-radius: 10px;
  background-color: #FFFFFF;
  box-shadow: 1px 1px 15px rgb(157, 150, 150);
  overflow: hidden;
  height: calc(100vh - 120px);
  max-height: 67vh;
  min-width: 500px;
}

.messages {
  position: relative;
  list-style: none;
  padding: 20px 10px 0 10px;
  margin: 0;
  height: calc(100vh - 250px);
  max-height: 55vh;
  overflow-y: auto;
}

.messages .message {
  clear: both;
  overflow: hidden;
  margin-bottom: 10px;
  transition: all 0.5s linear;
  opacity: 0;
}

.messages .message.left .avatar {
  background-color: #f5886e;
  float: left;
}

.messages .message.left .text_wrapper {
  background-color: #e1e1e1;
  margin-left: 20px;
  width: auto;
  max-width: 60%;
  font-family: Jost;
  font-size: 14px;
  border-radius: 10px;
}

.message.right.appeared{
  justify-content: right;
}
.messages .message.left .text {
  color: #c48843;
}

.messages .message.right .avatar {
  background-color: #fdbf68;
  float: right;
}

.messages .message.right .text_wrapper {
  background-color: #3f51b5;
  margin-right: 20px;
  float: right;
  width: auto;
  max-width: 60%;
  font-family: Jost;
  font-size: 14px;
  border-radius: 10px;
}

.messages .message.right .text {
  color: #FFFFFF;
}

.messages .message.left .text {
  color: black;
  font-weight: inherit;
}

.messages .message.appeared {
  opacity: 1;
  display: flex;
  align-content: center;
  align-items: center;
}

