/* navbar box */

.landing-nav {
	display: flex;
	align-items: center;
	height: 50px;
	font-family: "Cormorant Garamond";
	font-weight: bold;
	font-size: 14px;
	margin-bottom: 40px; /* moves the elements below the nav bar */
	position: fixed;
	width: 100%;
}

/* contact and email container */

.mini-flex-contactus {
	display: flex;
	flex-direction: row;
	color: white;
}

/* each item on the navbar except the logo*/

.landing-nav-links {
	display: block;
	/* testing purposes */
	/* background-color: turquoise; */
	position: relative;
	width: 100%;
	display: flex;
	justify-content: space-around;
	color: white;
	width: 25%;
}

.landing-nav-links li {
	list-style: none;
}

.landing-nav-links #link {
	color: white;
	text-decoration: none;
	letter-spacing: 1px;
	cursor: pointer;
}

.landing-nav-links #link:hover {
	color: burlywood;
}

/* body text */

